import { calc } from './calc';
import { card } from '././card';
import { communication } from './communication';
import { document } from './document';
import { lawconnect } from './lawconnect';
import { matter } from './matter';
import { msteams } from './msteams';
import { system } from './system';
import { app } from './app';
import { infotrack } from './infotrack';
import { accounting } from './accounting';
import { person } from './person';
import { register } from './register';
import { schema } from './schema';
import { staff } from './staff';
import { UserInfo } from '@leapdev/auth-agent/src/lib/types';

export interface BaseRequest {
  appSessionId?: string;
  close?: boolean;
}

export interface MatterBasedRequest extends BaseRequest {
  matterId: string;
}

export interface HostResponse {
  isSuccess: boolean;
  error: string;
  data: any;
}

export type MatterContext = {
  matterId: string;
  [key: string]: any;
};

export type CardContext = {
  cardId: string;
  [key: string]: any;
};

export type PersonContext = {
  personId: string;
  [key: string]: any;
};

export interface RequestContext {
  appSessionId: string; // leap-app session id - used for validation and host resource mapping
  host: string; // web, reporting, android, apple (4D by default)
  env: string; // dev, test, live
  region: string; // au, us, uk
  trustedApp?: boolean; // true for internal trusted applications
  newTab?: boolean; // true for opening on new tab
}

export class LeapContext {
  constructor(readonly context: MatterContext | CardContext | PersonContext, readonly hostInfo: HostInfo) {}
}

export class HostInfo {
  constructor(
    readonly appSessionId: string,
    readonly hostType: string,
    readonly hostVersion: string,
    readonly env: string,
    readonly region: string
  ) {}
}

export interface Api {
  accounting: accounting.Api;
  app: app.Api;
  calc: calc.Api;
  communication: communication.Api;
  document: document.Api;
  infotrack: infotrack.Api;
  lawconnect: lawconnect.Api;
  matter: matter.Api;
  msteams: msteams.Api;
  person: person.Api;
  register: register.Api;
  schema: schema.Api;
  system: system.Api;
  card: card.Api;
  staff: staff.Api;

  leapContext: LeapContext;

  init(leapAuthClientId?: string): Promise<void>;
  getRefreshedAccessToken(): Promise<string>;
  getDecodedRefreshedAccessToken(): Promise<any>;
  getUserInfo(): Promise<UserInfo>;
  registerHostMessageHandler(command: string, callback?: (param?: any) => void): void;
  deregisterHostMessageHandler(command: string): void;
}
