import {
  Api,
  LeapContext,
  HostResponse,
  RequestContext,
  MatterContext,
  HostInfo,
  CardContext,
  PersonContext,
} from '../api';
import { app } from '../api/app';
import { calc } from '../api/calc';
import { communication } from '../api/communication';
import { document } from '../api/document';
import { infotrack } from '../api/infotrack';
import { lawconnect } from '../api/lawconnect';
import { matter } from '../api/matter';
import { msteams } from '../api/msteams';
import { system } from '../api/system';
import { accounting } from '../api/accounting';
import { register } from '../api/register';
import { schema } from '../api/schema';
import { v4 as uuidv4 } from 'uuid';
import { config } from './config';
import { card } from 'api/card';
import { staff } from 'api/staff';
import { person } from 'api/person';
import { AuthAgent } from '@leapdev/auth-agent/src/index.umd';
import { decodeToken, getQueryStringValue } from './util';
import { UserInfo } from '@leapdev/auth-agent/src/lib/types';

export abstract class BaseApi implements Api {
  abstract get accounting(): accounting.Api;
  abstract get app(): app.Api;
  abstract get calc(): calc.Api;
  abstract get card(): card.Api;
  abstract get communication(): communication.Api;
  abstract get document(): document.Api;
  abstract get infotrack(): infotrack.Api;
  abstract get lawconnect(): lawconnect.Api;
  abstract get staff(): staff.Api;
  abstract get msteams(): msteams.Api;
  abstract get person(): person.Api;
  abstract get register(): register.Api;
  abstract get schema(): schema.Api;
  abstract get matter(): matter.Api;
  abstract get system(): system.Api;

  protected _leapContext: LeapContext;
  protected _isTrustedApp: boolean;

  private registeredCommands: { [command: string]: (params?: any) => void };
  private _userToken: string; //TODO: remove when LD2.4 is the minimum version

  constructor(requestContext: RequestContext) {
    this.registeredCommands = {};

    const hostInfo = new HostInfo(
      requestContext.appSessionId,
      requestContext.host,
      null,
      requestContext.env,
      requestContext.region
    );
    this._leapContext = new LeapContext(undefined, hostInfo);
    this._isTrustedApp = !!requestContext.trustedApp;
  }

  get leapContext(): LeapContext {
    return this._leapContext;
  }

  public get isTrustedApp(): boolean {
    return this._isTrustedApp;
  }

  public init(leapAuthClientId?: string): Promise<void> {
    //close the window if the user doesn't accept the permissions
    const errMsg = getQueryStringValue('error');
    if (errMsg === 'access_denied') this.system.close();

    return new Promise((resolve, reject) => {
      this.registerHostMessageHandler(
        this.leapContext.hostInfo.appSessionId,
        ({ data: leapContext }: leapContextResponse) => {
          this.deregisterHostMessageHandler(this.leapContext.hostInfo.appSessionId);
          const leapContextObj = {
            context: leapContext.context,
            hostInfo: this.leapContext.hostInfo,
          };

          if (!leapContext.context || (Object.keys(leapContext.context).length === 0 && !!leapContext.matterContext)) {
            leapContextObj.context = leapContext.matterContext;
          }

          let clientId = leapAuthClientId;

          //host info, available from LD2.4
          if (!!leapContext.hostInfo) {
            this._isTrustedApp = leapContext.isTrustedApp;

            leapContextObj.hostInfo = new HostInfo(
              this.leapContext.hostInfo.appSessionId,
              leapContext.hostInfo.hostType,
              leapContext.hostInfo.hostVersion,
              leapContext.hostInfo.env,
              leapContext.hostInfo.region
            );

            if (!this.isTrustedApp) {
              //use clientId from the schema for non-trusted apps
              if (!!leapContext.clientId) {
                clientId = leapContext.clientId;
              } else {
                return reject('Failed to load the clientId from the schema');
              }
            } else {
              //trusted apps don't need clientId
              this._leapContext = new LeapContext(leapContextObj.context, leapContextObj.hostInfo);
              this.hostRelatedSetup();
              return resolve();
            }
          } else {
            if (this.isTrustedApp) {
              //TODO: remove when 2.4 is the minimum version
              this._userToken = leapContext.authtoken;
              this._leapContext = new LeapContext(leapContextObj.context, leapContextObj.hostInfo);
              this.hostRelatedSetup();
              return resolve();
            }
          }

          this._leapContext = new LeapContext(leapContextObj.context, leapContextObj.hostInfo);
          this.hostRelatedSetup();

          const authEnv = leapContextObj.hostInfo.env.includes('live') ? 'live' : leapContextObj.hostInfo.env;
          const authHost = config.auth.host[authEnv][leapContextObj.hostInfo.region];

          AuthAgent.init({
            authHost: authHost,
            clientId: clientId,
            autoLogin: true,
            autoLogout: false,
            uniqueSession: false,
            scopes: ['*'], //AUTH-218, defaults to defaultScopes for marketplace apps
            autoRefreshToken: false,
          }).then((accessToken: string) => {
            return resolve();
          });
        }
      );
      this.retrieveContext();
    });
  }

  public async getRefreshedAccessToken(): Promise<string> {
    if (this.isTrustedApp) {
      if (!!this.leapContext.hostInfo.hostVersion) {
        //fall back to the default userToken if this command has not been implemented yet
        return this.system.send({ command: 'internal.getAccessToken' }, true) ?? this._userToken;
      } else {
        //TODO: remove when LD2.4 is the minimum version
        //only LD2.3 does not have the value for "hostVersion"
        return this._userToken;
      }
    }
    return AuthAgent.getRefreshedAccessToken();
  }

  public async getDecodedRefreshedAccessToken(): Promise<any> {
    const accessToken = await this.getRefreshedAccessToken();
    return decodeToken(accessToken);
  }

  public async getUserInfo(): Promise<UserInfo> {
    return AuthAgent.userInfo();
  }

  protected abstract retrieveContext(): void;
  protected abstract hostRelatedSetup(): void;

  public registerHostMessageHandler(command: string, callback?: (param?: any) => void) {
    const alreadyRegistered = !!this.registeredCommands[command];

    if (!alreadyRegistered) {
      this.registeredCommands[command] = callback;
    } else {
      throw new Error('Command already registered');
    }
  }

  public deregisterHostMessageHandler(command: string) {
    delete this.registeredCommands[command];
  }

  /**
   * A helper function that makes 2-way call easier. It takes care of registering and deregistering message handler for you.
   * @param request the actual request object from LEAP app
   * @param requestFn a function specifying how a request object should be sent to the host system. It passes the requestId as parameter so that the request function can attach it to the request object.
   * @param responseFn a function specifying how a raw response from the host system should be handled / transformed
   * @return a promise of the final response
   */
  public twoWayCall<I, O>(
    request: I,
    requestFn: (requestId: string, request: I) => void,
    responseFn: (response: any) => O
  ): Promise<O> {
    const requestId = uuidv4();
    return new Promise((resolve, reject) => {
      this.registerHostMessageHandler(requestId, (hostResponse: HostResponse) => {
        this.deregisterHostMessageHandler(requestId);
        if (hostResponse.isSuccess) {
          resolve(responseFn(hostResponse.data));
        } else {
          reject(new Error(hostResponse.error));
        }
      });
      requestFn(requestId, request);
    });
  }

  protected handleCommand(command: string, payload?: string) {
    const registeredCommand = this.registeredCommands[command];

    if (!!registeredCommand && !!payload) {
      const params = JSON.parse(payload);
      registeredCommand(params);
    } else if (!!registeredCommand) {
      registeredCommand();
    } else {
      throw new Error('Host command not registered');
    }
  }
}

interface leapContextResponse {
  isSuccess: boolean;
  data: leapContextInternal;
}

interface leapContextInternal {
  authtoken: string; //TODO: remove when 2.4 is the minimum version
  isTrustedApp: boolean;
  clientId: string;
  apiKey: string;
  context: any;
  matterContext: any; //deprecated, leave here for backwards compatible
  hostInfo: HostInfo;
}
