import { system } from '../../api/system';
import { BrowserFourdApi } from '.';
import { BaseRequest } from '../../api';

export class System implements system.Api {
  private _systemInfo: system.SystemInfo;

  constructor(private fourd: BrowserFourdApi) {}

  openTwitter(request: BaseRequest): void {
    this.fourd.call4Dv2('system.openTwitter', request);
  }

  close(): void {
    this.fourd.call4Dv2('system.close');
  }

  hide(): void {
    this.fourd.call4Dv2('system.hide');
  }

  wait(request?: system.MessageRequest): void {
    this.fourd.call4Dv2('system.wait', request);
  }

  resume(): void {
    this.fourd.call4Dv2('system.resume');
  }

  cancelClose(): void {
    this.fourd.call4Dv2('system.cancelClose');
  }

  alert(request: system.MessageRequest): void {
    if (!this.isString(request.message) || !request.message) {
      return;
    }
    this.fourd.call4Dv2('system.alert', request);
  }

  error(request: system.MessageRequest): void {
    if (!this.isString(request.message) || !request.message) {
      return;
    }
    this.fourd.call4Dv2('system.error', request);
  }

  openDialog(request: system.DialogRequest): Promise<boolean> {
    if (!this.isString(request.message) || !request.message) {
      throw new Error('Invalid value in message');
    } else if (!request.dialogType) {
      throw new Error('DialogType cannot be null');
    }

    return this.fourd.twoWayCall<system.DialogRequest, boolean>(
      request,
      (requestId, request) => {
        this.fourd.call4Dv2('system.openDialog', request, requestId);
      },
      (response) => {
        return response;
      }
    );
  }

  openUrl(config: system.BrowserConfig): void {
    config['action'] = 'OpenURL';
    this.fourd.call4Dv2('system.openUrl', config);
  }

  getSystemInfo(): Promise<system.SystemInfo> {
    if (this._systemInfo) {
      return Promise.resolve(this._systemInfo);
    }
    return this.fourd.twoWayCall<system.SystemInfo, system.SystemInfo>(
      null,
      (requestId, request) => {
        this.fourd.call4Dv2('system.getSystemInfo', request, requestId);
      },
      (response) => {
        this._systemInfo = response;
        return response;
      }
    );
  }

  setWindowTitle(request: system.SetWindowTitleRequest) {
    this.fourd.call4Dv2('system.setWindowTitle', request);
  }

  onBeforeClose(handler: () => Promise<boolean>): void {
    handler().then((shouldCancelClose) => {
      if (shouldCancelClose) {
        this.cancelClose();
      }
    });
  }

  async send(request: any, hasResponse: boolean = false): Promise<any> {
    if (!this.fourd.isTrustedApp) {
      throw new Error('Permission error');
    }

    if (request === null || request === undefined) {
      throw new Error('Bad request');
    }

    const isObject = typeof request === 'object';

    // Proxy generic command use case
    if (isObject && request['command']) {
      if (hasResponse) {
        return this.fourd.twoWayCall<any, any>(
          request,
          (requestId, request) => {
            this.fourd.call4Dv2(request['command'], request['request'], requestId);
          },
          (response) => {
            return response;
          }
        );
      } else {
        this.fourd.call4Dv2(request['command'], request['request']);
        return Promise.resolve();
      }
    }

    // For strings and SDK 1.0 commands
    if (hasResponse) {
      return this.fourd.twoWayCall<any, any>(
        request,
        (requestId, request) => {
          this.fourd.call4Dv2('system.send', request, requestId);
        },
        (response) => {
          return response;
        }
      );
    } else {
      this.fourd.call4Dv2('system.send', request);
      return Promise.resolve();
    }
  }

  // Legacy versions - Do not wrap request object in v2 format (LSDK-81)
  private sendLegacy(request: any): Promise<any> {
    const isObject = typeof request === 'object';
    if (!isObject || !request['requestId']) {
      this.fourd.call4D(this.fourd.to4DJSONString(request));
      return Promise.resolve();
    } else {
      return new Promise<any>((resolve, reject) => {
        this.fourd.registerHostMessageHandler(request['requestId'], (response) => {
          this.fourd.deregisterHostMessageHandler(request['requestId']);
          // Some handlers of 2.1 and 2.2 respond in v2 format
          if (typeof response === 'object' && !!response) {
            const hasStatus = response.hasOwnProperty('isSuccess');
            const hasError = response.hasOwnProperty('error');
            const hasData = response.hasOwnProperty('data');
            if (hasStatus && (hasError || hasData)) {
              if (response['isSuccess']) {
                resolve(response['data']);
              } else {
                reject(new Error(response['error']));
              }
            }
          } else {
            resolve(response);
          }
        });
        this.fourd.call4D(this.fourd.to4DJSONString(request));
      });
    }
  }

  finishLoading(): void {
    this.fourd.call4Dv2('system.finishLoading');
  }

  private isString = (value: any) => {
    return typeof value === 'string' || value instanceof String;
  };
}
