import { document } from '../../api/document';
import { BrowserFourdApi } from '.';

export class Document implements document.Api {
  constructor(private fourd: BrowserFourdApi) {}

  previewDocument(request: document.PreviewDocumentRequest): void {
    this.fourd.call4Dv2('document.previewDocument', request);
  }

  previewPrecedent(request: document.PreviewPrecedentRequest): void {
    this.fourd.call4Dv2('document.previewPrecedent', request);
  }

  createDocumentFromContainer(request: document.CreateDocumentFromContainerRequest): void {
    this.fourd.call4Dv2('document.createDocumentFromContainer', request);
  }

  createDocumentFromPrecedent(request: document.CreateDocumentFromPrecedentRequest): void {
    this.fourd.call4Dv2('document.createDocumentFromPrecedent', request);
  }

  openDocument(request: document.OpenDocumentRequest): void {
    this.fourd.call4Dv2('document.openDocument', request);
  }

  reloadDocumentList(request: document.ReloadDocumentListRequest): void {
    this.fourd.call4Dv2('document.reloadDocumentList', request);
  }

  editPrecedent(request: document.EditPrecedentRequest): void {
    this.fourd.call4Dv2('document.editPrecedent', request);
  }

  selectPrecedent(request: document.SelectPrecedentRequest): Promise<document.SelectPrecedentResponse> {
    return this.fourd.twoWayCall<document.SelectPrecedentRequest, document.SelectPrecedentResponse>(
      request,
      (requestId, request) => {
        this.fourd.call4Dv2('document.selectPrecedent', request, requestId);
      },
      (response) => response
    );
  }
}
