import { whitelistDomains, devDomains, leapHostTestAppDomain, jd } from './config/whitelistdomain';
import { BaseApi } from '../base-impl';
import { RequestContext } from '../../api';
import { accounting } from '../../api/accounting';
import { app } from '../../api/app';
import { calc } from '../../api/calc';
import { communication } from '../../api/communication';
import { document } from '../../api/document';
import { infotrack } from '../../api/infotrack';
import { lawconnect } from '../../api/lawconnect';
import { matter } from '../../api/matter';
import { msteams } from '../../api/msteams';
import { staff } from '../../api/staff';
import { card } from '../../api/card';
import { register } from '../../api/register';
import { schema } from '../../api/schema';
import { system } from '../../api/system';
import { person } from '../../api/person';

import { Accounting } from './accounting';
import { App } from './app';
import { Calc } from './calc';
import { Communication } from './communication';
import { Document } from './document';
import { Infotrack } from './infotrack';
import { LawConnect } from './lawconnect';
import { Matter } from './matter';
import { MsTeams } from './msteams';
import { Card } from './card';
import { Staff } from './staff';
import { Register } from './register';
import { Schema } from './schema';
import { System } from './system';
import { Person } from './person';

export class IframeWindow extends BaseApi {
  private _whiteListedHostDomains: string[];

  private _accounting: accounting.Api;
  private _app: app.Api;
  private _calc: calc.Api;
  private _communication: communication.Api;
  private _document: document.Api;
  private _infotrack: infotrack.Api;
  private _lawconnect: lawconnect.Api;
  private _matter: matter.Api;
  private _msteams: msteams.Api;
  private _card: card.Api;
  private _staff: staff.Api;
  private _register: register.Api;
  private _person: person.Api;
  private _schema: schema.Api;
  private _system: system.Api;

  get accounting(): accounting.Api {
    return this._accounting;
  }
  get app(): app.Api {
    return this._app;
  }
  get calc(): calc.Api {
    return this._calc;
  }
  get card(): card.Api {
    return this._card;
  }
  get communication(): communication.Api {
    return this._communication;
  }
  get document(): document.Api {
    return this._document;
  }
  get infotrack(): infotrack.Api {
    return this._infotrack;
  }
  get lawconnect(): lawconnect.Api {
    return this._lawconnect;
  }
  get matter(): matter.Api {
    return this._matter;
  }
  get msteams(): msteams.Api {
    return this._msteams;
  }
  get staff(): staff.Api {
    return this._staff;
  }
  get person(): person.Api {
    return this._person;
  }
  get register(): register.Api {
    return this._register;
  }
  get schema(): schema.Api {
    return this._schema;
  }
  get system(): system.Api {
    return this._system;
  }

  private parentWindow: Window;

  constructor(requestContext: RequestContext, newTab: boolean = false) {
    super(requestContext);

    this._accounting = new Accounting(this);
    this._app = new App(this);
    this._calc = new Calc(this);
    this._card = new Card(this);
    this._communication = new Communication(this);
    this._document = new Document(this);
    this._infotrack = new Infotrack(this);
    this._lawconnect = new LawConnect(this);
    this._matter = new Matter(this);
    this._msteams = new MsTeams(this);
    this._person = new Person(this);
    this._register = new Register(this);
    this._schema = new Schema(this);
    this._staff = new Staff(this);
    this._system = new System(this);

    this.parentWindow = newTab ? window.opener : window.parent;
    this.registerMessageEventListener();
  }

  protected retrieveContext(): void {
    this.postMessage({
      command: 'RetrieveLeapAppContext',
      args: {
        requestId: this.leapContext.hostInfo.appSessionId,
      },
    });
  }

  protected hostRelatedSetup(): void {
    if (this.leapContext.hostInfo.hostType === 'web') {
      this._whiteListedHostDomains = [
        ...(whitelistDomains[`${this.leapContext.hostInfo.env}_${this.leapContext.hostInfo.region}`] || []),
        ...devDomains,
        leapHostTestAppDomain,
        jd,
      ];
    }
  }

  postMessage(message: { command: string; args?: any; sdkVersion?: number }): void {
    // Always append appSessionId to the message args sent to Host for validation
    message.args = {
      ...(message.args || {}),
    };

    if (this.leapContext?.hostInfo?.appSessionId) message.args.appSessionId = this.leapContext.hostInfo?.appSessionId;

    message.sdkVersion = message.sdkVersion ?? 2;
    this.parentWindow.postMessage(message, '*');
  }

  private registerMessageEventListener() {
    window.addEventListener('message', this.receiveMessage.bind(this), false);
  }

  private receiveMessage(event) {
    // Origin is not white-listed, ignore message
    // mobile doesn't have host
    if (this._whiteListedHostDomains?.length > 0 && !this._whiteListedHostDomains.includes(event.origin)) {
      return;
    }

    if (!!event.data.command) {
      const command: string = event.data.command;
      if (!!event.data.payload) {
        super.handleCommand(command, event.data.payload);
      } else {
        super.handleCommand(command);
      }
    }
  }
}
